import {homeData} from '@/utils/data.js'

const formatDate = date => {
  const year = date.getFullYear()
  const month = date.getMonth() + 1
  const day = date.getDate()
  let week = date.getDay()
  switch (week) {
  			case 0:
  				week = '周日'
  				break;
  			case 1:
  				week = '周一'
  				break;
  			case 2:
  				week = '周二'
  				break;
  			case 3:
  				week = '周三'
  				break;
  			case 4:
  				week = '周四'
  				break;
  			case 5:
  				week = '周五'
  				break;
  			case 6:
  				week = '周六'
  				break;
  		}

  return [year, month, day].map(formatNumber).join('-') + ' ' + week
}

const formatTime = date => {
	const hour = date.getHours()
  const minute = date.getMinutes()
  const second = date.getSeconds()

  return [hour, minute, second].map(formatNumber).join(':')
}

const handleSave = (id) => {
	let item = findItem(homeData(), id)
	let saveArr = JSON.parse(localStorage.getItem('save') ? localStorage.getItem('save') : JSON.stringify([]))
	let index = haveItem(saveArr, id)
	if (!index) {
		saveArr.unshift(item)
		// console.log(saveArr)
		localStorage.setItem('save', JSON.stringify(saveArr))
	} else {
		saveArr.splice(Number(index), 1)
		// console.log(saveArr)
		localStorage.setItem('save', JSON.stringify(saveArr))
	}
}
const findItem = (arr, key) => {
	for (let i=0; i<arr.length; i++) {
		let subarr = arr[i];
		for (let j=0; j<subarr.length; j++) {
			if (subarr[j].id == key) {
				return subarr[j]
			}
		}
	}
}
const haveItem = (arr, key) => {
	for (let i=0; i<arr.length; i++) {
		if (arr[i].id == key) {
			return i.toString()
		}
	}
	return false
}

