<template>
	<div>
		<div class="bar">
			<span class="hr"></span>
			<span>常用功能</span>
		</div>
		<div class="stock">
			<div v-if="ifHaveItem">
				<div
					class="item"
					@click="toDetailPage(item.id)"
					v-for="item of stock"
					:key="item.id"
				>
					<div class="item-icon">
						<img class="item-content" :src="item.icon" />
					</div>
					<div class="item-txt">{{item.desc}}</div>
				</div>
			</div>
			<div v-else class="info">还没有添加常用功能</div>
		</div>
	</div>
</template>

<script>
export default {
	name: 'Stock',
	data () {
		return {
			stock: [],
			ifHaveItem: true
		}
	},
	mounted () {
		let saveArr = JSON.parse(localStorage.getItem('save') ? localStorage.getItem('save') : JSON.stringify([]));
		if (!saveArr.length) {
			this.ifHaveItem = false
		}
		this.stock = saveArr
	}
}
</script>

<style lang="stylus" scoped>
@import '~styles/varible.styl'
	.bar
		height: .8
		line-height: .86rem
		background: #fff
		margin: .2rem 0
		font-family: $fontFamily
		font-size: 18px
		.hr
			hr()
	.stock
		overflow hidden
		width: 100%
		height: 0
		padding-bottom: 27%
		background: #fff
		.info
			text-align center
			font-size 18px
			padding-top 20px
		.item
			position: relative
			float: left
			width: 25%
			padding-bottom: 25%
			box-sizing: border-box
		.item-icon
			position: absolute
			top: 0
			left: 0
			right: 0
			bottom: .3rem
			.item-content
				width: 100%
				box-sizing: border-box
				display: block
				margin: 0 auto
				padding: .2rem .32rem
		.item-txt
			position: absolute
			left: 0
			right: 0
			bottom: 0
			line-height: .3rem
			text-align: center
</style>
