<template>
  <div class="bar">
      <span class="hr"></span>
      <span>{{name}}</span>
      <span class="bar-time" v-if="time">{{barTime}}</span>
  </div>
</template>

<script>
export default {
  name: 'CommonBar',
  data () {
    return {
      barTime: ''
    }
  },
  props: {
    name: String,
    time: Boolean
  },
  mounted () {
    this.barTime = this.formatTime(new Date());
  	this.timer = setInterval(() => {
  		this.barTime = this.formatTime(new Date());
  	}, 1000)
  },
  beforeDestroy () {
  	// 在页面销毁时，清除定时器
  	if (this.timer) {
  		clearInterval(this.timer);
  	}
  }
}
</script>

<style lang='stylus' scoped>
@import '~styles/varible.styl'
  .bar
    line-height: .64rem
    background: #fff
    margin: .1rem 0
    font-family: '楷体', serif
    font-size: 16px
    box-sizing border-box
    .hr
      hr()
    .bar-time
      float right
      margin-right 10px
</style>