<template>
    <div class="parent">
		<el-collapse v-model="activeNames" class="collapse">
            <el-collapse-item title="请选择参数" name="1">
				<el-form :model="queryForm" :rules="queryRules" ref="queryForm" label-width="100px" class="query-container">
					<el-form-item label="查询位置" prop="locate">
						<el-radio-group v-model="queryForm.locate" @change="handleLocateChange">
							<el-radio
								v-for="item in locateOptions"
								:key="item.value"
								:label="item.value"
							>{{item.label}}</el-radio>
						</el-radio-group>
					</el-form-item>
					<el-form-item v-if="device" label="查询设备" prop="device">
						<el-select v-model="queryForm.device" placeholder="请选择" @change="handleDeviceChange" @blur="handleDeviceBlur">
							<el-option
								v-for="item in deviceOptions"
								:key="item.value"
								:label="item.label"
								:value="item.value"
							>
							</el-option>
						</el-select>
					</el-form-item>
					<el-form-item v-if="frequency" label="更新频率" prop="reviewtime">
						<el-select v-model="queryForm.reviewtime" placeholder="请选择" @change="handleTimeChange">
							<el-option
								v-for="item in reviewOptions"
								:key="item.value"
								:label="item.label"
								:value="item.value"
							>
							</el-option>
						</el-select>
					</el-form-item>
                    <el-form-item>
                        <!-- <input type="date"> -->
                    </el-form-item>
					<el-form-item>
						<el-button type="primary" @click="submitForm('queryForm')">查询</el-button>
					<el-button @click="resetForm">重置</el-button>
					</el-form-item>
				</el-form>
			</el-collapse-item>
		</el-collapse>
		<div class="hasSelect" v-if="showSelect">
			已选择:
			<span class="hasSelect-device">{{chooseName}}</span>
			<span class="hasSelect-review">更新频率: {{chooseTime}}s</span>
		</div>
    </div>
</template>

<script>
export default {
	name: 'Form',
	props: {
        formData: Array,
        device: {
            default: false,
            type: Boolean
        },
        frequency: {
            default: false,
            type: Boolean
        },
	},
    data () {
        return {
            activeNames: '1',
            locateOptions: [],
            deviceOptions: [],
            reviewOptions: [
                {
                    label: '4s',
                    value: '4000',
                }, {
                    label: '5s',
                    value: '5000'
                }, {
                    label: '6s',
                    value: '6000'
                }, {
                    label: '10s',
                    value: '10000'
                }
            ],
            rangeDate: '',
            queryForm: {
                locate: '',
                device: '',
                reviewtime: ''
            },
            queryRules: {
                device: [
                    { required: true, message: '请选择查询设备', trigger: 'blur' }
                ],
                reviewtime: [
                    { required: true, message: '请选择更新频率', trigger: 'blur' }
                ]
			},
			deviceId: '',
			period: '',
			showSelect: false,
			chooseName: '',
			chooseTime: ''
        }
    },
    methods: {
        submitForm(formName) {
			this.$refs[formName].validate((valid) => {
				if (valid) {
					// 收起选择参数表
					this.activeNames = '';
					// 传递参数
					let obj = {};
					obj.deviceId = this.deviceId;
					obj.period = this.period;
					// console.log(obj)
					this.$emit("showChart", obj);
					this.showSelect = true;
				}
			});
		},
		resetForm () {
			this.queryForm = {
			  	locate: '01',
				device: '',
				reviewtime: ''
		  	}
		},
		handleLocateChange (value) {
			let formData = this.formData;
			// console.log(value)
			for (let i=0; i<formData.length; i++) {
				// console.log(formData[i])
				if (formData[i].name == value) {
					this.deviceOptions = formData[i].children
					return;
				}
			}
		},
		handleDeviceChange (value) {
			// console.log(value, this.deviceOptions)
			this.deviceId = value;
		},
		handleDeviceBlur (e) {
			// console.log(e);
			this.chooseName = e.srcElement.value;
		},
		handleTimeChange (value) {
			this.chooseTime = value / 1000;
			this.period = value
		},
		toFormData () {
			let formData = this.formData,
				length= formData.length;
			let locateArr = [];
			for (let i=0; i<length; i++) {
				let obj = {},
				name = formData[i].name;
				obj.label = name
				obj.value = name
				locateArr.push(obj)
			}
			this.locateOptions = locateArr
			// console.log(locateArr[0].name)
			this.queryForm.locate = locateArr[0].label
			this.deviceOptions = formData[0].children
			// this.deviceId = formData[0].children.value
		}
	},
	mounted () {
        // console.log(this.device)
		this.toFormData();
		// console.log(this.formData)
	}
}
</script>

<style lang="stylus" scoped>
@import '~styles/varible.styl'
	.parent
		width 100%
		overflow hidden
		.collapse >>> .el-collapse-item__header
			padding 0 .2rem
		.collapse >>> .el-collapse-item__header::before
			content ''
			hr()
		.collapse
			margin .1rem 0 0
			.query-container
				width 100%
				background #ffffff
				padding .2rem 0 0
		.hasSelect
			width 100%
			padding	.2rem .4rem
			background #ffffff
			span
				margin-left .2rem
</style>

