import router from '@/router'
// import Cookies from 'js-cookie'

const toDetailPage = id => {
    // if (!Cookies.get('Token')) {
    //     router.push({path: '/login'});
    //     return true;
    // }
    switch (id) {
        case 'custom':
            router.push({path: '/datacustom'});
            break;
        case 'tips':
            router.push({path: '/datatips'});
            break;
        case '01':
            router.push({path: '/onlinespy', query: {id: '01'}});
            break;
        case '02':
            router.push({path: '/historydata', query: {id: '02'}});
            break;
        case '03':
            router.push({path: '/eventRecord', query: {id: '03'}});
            break;
        case '04':
            router.push({path: '/energyMark', query: {id: '04'}});
            break;
        case '05':
            router.push({path: '/productLine', query: {id: '05'}});
            break;
        case '06':
            router.push({path: '/deviceAnalysis', query: {id: '06'}});
            break;
    }
}

export default toDetailPage;