<template>
	<div class="info">
		<div class="info-date">
			<div class="info-date-time">{{date}}</div>
			<div class="info-date-detail">{{time}}</div>
		</div>
		<div class="info-custom" @click="toDetailPage('tips')">
			<div class="info-custom-num">0</div>
			<div>监控提示</div>
		</div>
		<div class="info-custom" @click="toDetailPage('custom')">
			<div class="info-custom-num">dizao</div>
			<div>欢迎使用</div>
		</div>
	</div>
</template>

<script>
export default {
  name: 'HeaderInfo',
  data () {
  	return {
  		date: '',
  		time: ''
  	}
  },
  mounted () {
	this.date = this.formatDate(new Date());
	this.time = this.formatTime(new Date());
  	this.timer = setInterval(() => {
  		this.time = this.formatTime(new Date());
  	}, 1000)
  },
  beforeDestroy () {
  	// 在页面销毁时，清除定时器
  	if (this.timer) {
  		clearInterval(this.timer);
  	}
  }
}
</script>

<style lang="stylus" scoped>
@import '~styles/varible.styl'
	.info
		position: relative
		height: 1.2rem
		background: #fff
		.info-date
			position: absolute
			top: 0
			left: -12%
			width: 55%
			height: 100%
			text-align: center
			background: $barColor
			color: #fff
			border-radius: 1rem
			padding-left: .2rem
			.info-date-time
				line-height: .7rem
			.info-date-detail
				line-height: .5rem
				font-size: .4rem
		.info-custom
			width: 20%
			padding: 0 .3rem
			text-align: center
			float: right
			.info-custom-num
				font-size: .4rem
				line-height: .8rem
</style>
