<template>
	<el-form
		class="container"
		v-loading="loading"
		element-loading-text="拼命加载中"
		element-loading-spinner="el-icon-loading"
		element-loading-background="rgba(0, 0, 0, 0.8)">
		<common-header name="实时监控" @save="handleSave($route.query.id)"></common-header>
		<detail-form @showChart="handleGetChartData" :formData="formData" v-if="showForm" :device="true" :frequency="true"></detail-form>
		<div v-if="ifShowChart">
			<common-bar name="实时用电信息" :time=true></common-bar>
			<detail-table  :tableData="tableData"></detail-table>
			<common-bar :name="chartName" :time=true></common-bar>
			<detail-chart :chartVoltData="chartVoltData" :chartElecData="chartElecData" @changeChartTitle="changeChartTitle"></detail-chart>
		</div>
	</el-form>
</template>

<script>

import CommonHeader from '@/components/CommonHeader'
import CommonBar from '@/components/CommonBar'
import DetailForm from '@/components/CommonForm'
import DetailTable from './components/Table'
import DetailChart from './components/Chart'
import axios from 'axios'
import { setInterval, clearInterval, setTimeout, clearTimeout } from 'timers';
import { constants } from 'crypto';

export default {
	name: 'onlineSpy',
	components: {
		CommonHeader,
		DetailForm,
		CommonBar,
		DetailTable,
		DetailChart
	},
	data () {
		return {
			ifShowChart: false,
			showForm: false,
			formData: [],
			tableData: [],
			chartName: '谐波电压畸变率',
			chartVoltData: [],
			chartElecData: [],
			loading: true,
			intervalTime: '',
			deviceId: ''
		}
	},
	mounted () {
		// this.getFormData();
		// 页面挂载后获取栏目数据
		this.formData = JSON.parse(sessionStorage.getItem("formData"));
		this.loading = false;
		this.showForm = true;
	},
	methods: {
		// 查询按钮点击后触发的事件
		handleGetChartData (obj) {
			this.loading = true;
			// obj 包含设备ID和刷新时间参数
			this.intervalTime = obj.period;
			this.deviceId = obj.deviceId;
			// 清除之前定时器
			if (this.timer) {
				clearTimeout(this.timer)
			}
			setTimeout(() => {
				// 获取实时数据
				axios.get('http://120.78.219.211/meter/device/getRealData.action', {
					params: {
						deviceId: 'dv299958288827822080'
					}
				}).then(this.getTableDataSuccess)
				axios.get('http://120.78.219.211/meter/device/getHarmoincWave.action', {
					params: {
						deviceId: 'dv299958288827822080',
						type: 0
					}
				}).then(this.getChartDataSuccess)
				// 触发刷新数据事件
				this.timer = setTimeout(this.handleRefreshPageData, obj.period)
			}, 300)
		},
		handleRefreshPageData () {
			if (this.timer) {
				clearTimeout(this.timer)
			}
			let time = new Date().getTime()
			axios.get('http://120.78.219.211/meter/device/getRealData.action', {
					params: {
						deviceId: 'dv299958288827822080'
					}
				}).then(this.getTableDataSuccess)
			axios.get('http://120.78.219.211/meter/device/getHarmoincWave.action', {
				params: {
					deviceId: 'dv299958288827822080',
					type: 0
				}
			}).then(this.getChartDataSuccess)
			this.timer = setTimeout(this.handleRefreshPageData, this.intervalTime)
		},
		getTableDataSuccess (res) {
			// console.log(res.data.data)
			this.tableData = res.data.data.baseData
		},
		getChartDataSuccess (res) {
			res = res.data.data;
			// console.log(res);
			let voltArr = [], elecArr = [];
			// 处理chart数据
			for (let i=0; i<res.length; i++) {
				// console.log(res[i])
				if (i < 3) {
					voltArr.push(res[i].data)
				} else {
					elecArr.push(res[i].data)
				}
			}
			this.chartVoltData = voltArr;
			this.chartElecData = elecArr;
			if (this.loading) {
				this.ifShowChart = true;
				this.loading = false;
			}
		},
		changeChartTitle (val) {
			this.chartName = val;
		}
	},
	beforeDestroy () {
		// 页面销毁后，清除间隔调用
		if (this.timer) {
			clearTimeout(this.timer);
		}
	}
}
</script>

<style lang="stylus" scoped>
	.container
		width 100%
		height 100%
		.chart
			width 100%
			height 500px
</style>
