<template>
  <div class="home">
    <home-header></home-header>
    <home-info></home-info>
    <home-section></home-section>
    <home-stock></home-stock>
		<!-- <img alt="Vue logo" src="@/assets/images/change.png"> -->
  </div>
</template>

<script>

	import HomeHeader from './components/Header'
	import HomeInfo from './components/Info'
	import HomeSection from './components/Section'
	import HomeStock from './components/Stock'
	import axios from 'axios'

  export default {
		name: 'Home',
		data () {
			return {
				// formData: []
			}
		},
    components: {
    	HomeHeader,
    	HomeInfo,
    	HomeSection,
			HomeStock
    },
    methods: {
			sendData (arr) {
				for (let i=0; i<arr.length; i++) {
					let children = arr[i].children;
					if (children) {
						for (let j=0; j<children.length; j++) {
							children[j].label = children[j].name;
							children[j].value = children[j].deviceId;
						}
					}
				}
				// console.log(arr)
				// 会话存储formData数据
				sessionStorage.setItem('formData', JSON.stringify(arr))
			},
			getFormData () {
				axios.get('http://120.78.219.211/meter/category/getTreeCategory.action?hasCompany=false&endNode=4&companyId=1').then(res => {
					var formData = [];
					res = res.data.data;
					for (let i=0; i<res.length; i++) {
						let children = res[i].children;
						for (let j=0; j<children.length; j++) {
							formData.push(children[j])
						}
					}
					this.sendData(formData);
				})
			},
    },
    mounted () {
			// 获取栏目数据
			this.getFormData();
    }
  }	
</script>
