<template>
	<div class="parent">
		<div class="bar">
			<span class="hr"></span>
			<span>{{title}}</span>
		</div>
		<div class="section">
			<swiper :options="swiperOption">
				<swiper-slide
					v-for="(page, index) of pages"
					:key="index"
				>
					<div
						class="item"
						@click="toDetailPage(item.id)"
						v-for="item of page"
						:key="item.id"
					>
						<div class="item-icon">
							<img class="item-content" :src="item.icon" />
						</div>
						<div class="item-txt">{{item.desc}}</div>
					</div>
				</swiper-slide>
				<div class="swiper-pagination"  slot="pagination"></div>
			</swiper>
		</div>
	</div>
</template>

<script>
export default {
	name: 'Section',
	data () {
		var _this = this
		return {
			title: '在线监控',
			swiperOption: {
			autoplay: false,
			pagination: '.swiper-pagination',
			paginationClickable :true,
			onSlideChangeEnd: function (swiper) {
				// swiper切换时更改标题
				switch (swiper.activeIndex) {
					case 0:
						_this.title = '在线监控'
						break;
					case 1:
						_this.title = '系统分析'
						break;
					case 2:
						_this.title = '系统管理'
						break;
					}
				}
			},
			pages: []
		}
	},
	mounted () {
		this.pages = this.gethomeData()
		// var item = document.getElementsByClassName('item')
		// // 给item绑定长按事件
		// var _this = this
		// this.longPress(item, function () {
		//   console.log(this)
		//   _this.$confirm('将此功能添加到常用功能', '提示', {
		//       confirmButtonText: '确定',
		//       cancelButtonText: '取消',
		//       type: 'warning'
		//     }).then(() => {
		//       // localStorage.setItem('name', 'billy')
		//       _this.$message({
		//         type: 'success',
		//         message: '添加成功!'
		//       });
		//     }).catch(() => {
						
		//     });
		// })
	}
}
</script>

<style lang="stylus" scoped>
@import '~styles/varible.styl'
	.parent
		width: 100%
		overflow: hidden
		.bar
			line-height: .86rem
			background: #fff
			margin: .2rem 0
			font-family: $fontFamily
			font-size: 18px
			.hr
				hr()
		.section >>> .swiper-slide
			height: 0
			padding-bottom: 52%
		.section >>> .swiper-container
			overflow: visible
		.section >>> .swiper-pagination-bullets
			top: -36px
			right: 0
			left: inherit
			width: 20%
		.section
			width: 100%
			height: 0
			padding-bottom: 52%
			background: #fff
			.item
				position: relative
				float: left
				width: 25%
				padding-bottom: 25%
				box-sizing: border-box
			.item-icon
				position: absolute
				top: 0
				left: 0
				right: 0
				bottom: .3rem
				.item-content
					width: 100%
					box-sizing: border-box
					display: block
					margin: 0 auto
					padding: .2rem .32rem
			.item-txt
				position: absolute
				left: 0
				right: 0
				bottom: 0
				line-height: .3rem
				text-align: center
</style>
