<template>
	<div class="header">
		<router-link to="/home" class="header-left">
			<div class="icon-back iconfont">&#xe61a;</div>
		</router-link>
		<div class="header-title">{{name}}</div>
		<div class="header-right" @click="handleSave" v-if="loveShow">
			<div class="icon-love iconfont">&#xe6ca;</div>
		</div>
	</div>
</template>

<script>
export default {
  	name: 'CommonHeader',
  	data () {
		return {
			ifSave: false
		}
  	},
	props: {
		name: String,
		id: String,
		loveShow: {
			default: true,
			type: Boolean
		}
	},
	methods: {
		handleSave () {
			let dom = document.getElementsByClassName('icon-love')[0];
			if (this.ifSave) {
				dom.style.color = '#ffffff'
			} else {
				this.$message({
					message: '已添加到常用功能',
					type: 'success'
				})
				dom.style.color = '#f00'
			}
			this.ifSave = !this.ifSave
			this.$emit("save")
		}
	},
	mounted () {
		// 获取用户收藏的功能
		let id = this.$route.query.id
		let saveArr = JSON.parse(localStorage.getItem('save') ? localStorage.getItem('save') : JSON.stringify([]))
		if (!saveArr.length) { return true }
		for (let i=0; i<saveArr.length; i++) {
			if (saveArr[i].id == id) {
				// console.log(true)
				this.ifSave = true
				document.getElementsByClassName('icon-love')[0].style.color = '#f00'
			}
		}
	}
}
</script>

<style lang="stylus" scoped>
@import '~styles/varible.styl'
	.header
		position: relative
		line-height: $headerHeight
		background: $bgColor
		color: $txtColor
		.header-left
			position: absolute
			top: 0
			left: 0
			width: $headerHeight
			height: 100%
			color: #fff
			.icon-back
				font-size: .5rem
				text-align: center
		.header-title
			text-align: center
			font-family: $fontFamily
			font-size: $fontSize
		.header-right
			position: absolute
			top: 0
			right : 0
			width: $headerHeight
			height: 100%
			color: #fff
			.icon-love
				font-size: .5rem
				text-align: center
</style>
