import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

import fastclick from 'fastclick'
import VueAwesomeSwiper from 'vue-awesome-swiper'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import 'swiper/dist/css/swiper.css'
import 'styles/border.css'
import 'styles/reset.css'
import 'styles/iconfont.css'
// 引入日期处理函数
import {formatDate, formatTime, handleSave} from './utils/util.js'
import {homeData} from '@/utils/data.js'
// 引入路由跳转函数
import toDetailPage from './utils/router.js'

Vue.config.productionTip = false;
fastclick.attach(document.body);
Vue.use(VueAwesomeSwiper);
Vue.use(ElementUI);

Vue.prototype.formatDate = formatDate
Vue.prototype.formatTime = formatTime
Vue.prototype.toDetailPage = toDetailPage
Vue.prototype.gethomeData = homeData
Vue.prototype.handleSave = handleSave

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
