<template>
	<el-table
		class="table"
		:data="proTableData"
		style="width: 100%;"
		:stripe="stripe"
		size="mini"
		height=240
		:cell-style="isCenter">
		<el-table-column
			fixed
			prop="func"
			label="功能"
			width="80px">
		</el-table-column>
		<el-table-column
			prop="l1s"
			label="L1最小值"
			width="70px">
		</el-table-column>
		<el-table-column
			prop="l1m"
			label="L1平均值"
			width="70px">
		</el-table-column>
		<el-table-column
			prop="l1b"
			label="L1最大值"
			width="70px">
		</el-table-column>
		<el-table-column
			prop="l2s"
			label="L2最小值"
			width="70px">
		</el-table-column>
		<el-table-column
			prop="l2m"
			label="L2平均值"
			width="70px">
		</el-table-column>
		<el-table-column
			prop="l2b"
			label="L2最大值"
			width="70px">
		</el-table-column>
		<el-table-column
			prop="l3s"
			label="L3最小值"
			width="70px">
		</el-table-column>
		<el-table-column
			prop="l3m"
			label="L3平均值"
			width="70px">
		</el-table-column>
		<el-table-column
			prop="l3b"
			label="L3最大值"
			width="70px">
		</el-table-column>
		<el-table-column
			prop="lns"
			label="Ln最小值"
			width="70px">
		</el-table-column>
		<el-table-column
			prop="lnm"
			label="Ln平均值"
			width="70px">
		</el-table-column>
		<el-table-column
			prop="lnb"
			label="Ln最大值"
			width="70px">
		</el-table-column>
		<el-table-column
			prop="sums"
			label="合计最小值"
			width="70px">
		</el-table-column>
		<el-table-column
			prop="summ"
			label="合计平均值"
			width="70px">
		</el-table-column>
		<el-table-column
			prop="sumb"
			label="合计最大值"
			width="70px">
		</el-table-column>
	</el-table>
</template>

<script>
export default {
	name: 'Table',
	data() {
		return {
			stripe: true,
			highlight: true,
			proTableData: []
		}
	},
	props: {
		tableData: Array
	},
	watch: {
		// 监听数据变化
		tableData () {
			this.proTableData = this.changeTableData();
		}
	},
	mounted () {
		// 处理父组件传过来的数据
		this.proTableData = this.changeTableData();
	},
	methods: {
		changeTableData () {
			let data = this.tableData;
			if (data.length > 1) {
				let tableData = this.tableData.slice(1),
				length = tableData[0].length,
				newData = [];
				var keys = ['func' ,'l1s', 'l1m', 'l1b', 'l2s', 'l2m', 'l2b', 'l3s', 'l3m', 'l3b', 'lns', 'lnm', 'lnb', 'sums', 'summ', 'sumb']
				// console.log(data[0].length, keys.length)
				for (var i=0; i<tableData.length; i++) {
					var obj = {}
					for (var j=0; j<length; j++) {
					obj[keys[j]] = tableData[i][j];
					}
					newData.push(obj)
				}
				// console.log(newData)
				return newData
			}
		},
		isCenter (row, column, rowIndex, columnIndex) {
			return 'border-right: 1px solid #ccc; line-height: 12px'
		}
	}
}
</script>

<style scoped lang="stylus">
	.table >>> .cell
		line-height 15px !important
</style>
